html{
  height: 100%;
  background-image: linear-gradient(#176aa7,#5696c1);
  background-repeat: no-repeat;
  font-family: sans-serif;
  color: #0f222d;
  background-size: auto;
}

.main{
  width: 400px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: auto;
}
h1{
  text-align: center;
  color: #fff;
}

form{
  background-color: #fff;
  padding: 15px;
  box-shadow: 0 13px 10px -10px #2f4f4f, 0 -10px 10px -10px #2f4f4f;
}

input[type="text"]{
  width: 275px;
  margin-left: 20px;
  margin-bottom: 5px;
  height: 30px;
  outline: 0;
  border: none;
  font-size: 1.5em;
  background: #fff;
  color: #2f4f4f;
}

button{
  font-size: 2.5em;
  color: #53BDFF;
  background-color: #fff;
  outline: 0;
  border: none;
  width: 50px;
  margin-top:-10px;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #d3d3d3;
  opacity: 1; /* Firefox */
}

.tasksBoard{
  background-color: #fff;
  padding-bottom: 10px;
  padding-right: 40px;
  display: none;
  box-shadow: 0 15px 10px -10px #2f4f4f;
  /* border-top: 1px solid #d3d3d3; */
}


ul{
  list-style: none;
  max-height: 50vh;
  overflow: scroll;
  border-bottom: 1px solid #d3d3d3;
  border-top: 1px solid #d3d3d3;
  width: 100%;
  box-shadow: inset 0 7px 5px -7px #d3d3d3, inset 0 -7px 5px -7px #d3d3d3;
}
li{
  border-bottom: 1px solid #d3d3d3;
  padding-bottom: 15px;
  padding-top: 15px;
  font-size: 1.3em;
  color: #2f4f4f;
  width: 90%;
}
a{
  float: right;
  text-align: right;
  color: #ff6161;
  font-size: 1.2em;
  cursor: pointer;
  margin-bottom: 10px;
}
.delete{
  background-color: #ff6161;
  padding: 0px 7px 2px 7px;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
  float: right;
  margin-right: 10px;
  margin-top: 3px;
}

.cbox{
  width: 2.3em;
  height: 2.3em;
  background-color: white;
  border-radius: 50%;
  vertical-align: middle;
  border: 1px solid #2f4f4f;
  -webkit-appearance: none;
  outline: none;
  cursor: pointer;
  margin-top: 0px;
  transition: all .2s;
}

.cbox:checked{
 border-color: #d3d3d3;
 background-image:  url('./img/tick.png');
 background-size: cover;
}

@media (max-width: 500px) {
  .main{
    margin: auto;
    width: 100%;
  }
  input[type="text"]{
    width: 80%;
  }
  button{
    width: 10%;
  }
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
